<template>
    <div id="rentDetailShare">
        <assistant :cityId="cityId" :agent="apiData.houseAgent" :callBigDataParams="getUvParamsString({ eventName : 2057002})" :wechatBigDataParams="getUvParamsString({ eventName : 2057008 })"/>
        <download-app :downloadBigDataParams="getUvParamsString({ eventName : 2057003})"></download-app>
       <!---->
       <!--3.6-->
        <!--<swiper :options="pageConfs.swiperOption">
            <swiper-slide v-for="(slide , index) in apiData.houseImages" :key="index">
                <video :src="slide.videoSrc" :poster="slide.imageSrc" controls="controls" preload="none"  class="img-responsive" style="width:100%;height : 210px ; " v-if="slide.video"></video>                
                <img :src="slide" class="img-responsive" v-else>
                <div class="pagination">{{ pageStates.swiperActiveIndex }} / {{ apiData.houseImages.length }}</div>
            </swiper-slide>
        </swiper> -->

        <!--3.7-->
        <swiper :options="pageConfs.swiperOption">            
            <swiper-slide style="text-align:center" v-for="(slide , index) in houseImageAndVideoList" :key="slide.url">                
                <!-- <video :src="slide.videoUrl" :poster="slide.videoSmallImage" controls="controls" preload="none"  class="img-responsive" style="width:100%;height : 210px ; " v-if="slide.isVideo"></video> -->
                <template  v-if="slide.isVideo">
                    <div style="position:relative" @click="playVideo(slide.video)">                    
                        <img style="margin:0 auto;dislay:block;" src="https://imgwater-test.oss.aliyuncs.com/6af6e136ce64436598f5e016bc9378f3.DL" class="img-responsive"> 
                        <div style="display:flex;justify-content:center;align-items:center;position:absolute;left:50%;top:50%;margin-left:-30px;margin-top:-30px;width:60px;height:60px;border-radius:50%;background-color:rgba(0,0,0,.5)">
                            <div style="width:0;height:0;border-top:14px solid transparent;border-left:20px solid rgba(255,255,255,1);border-bottom:14px solid transparent;margin-left:4px;">

                            </div>
                        </div>
                    </div>
                </template>
                               
                <img @click="previewImage()" :src="slide.url" class="img-responsive" v-else>
                <div class="pagination">{{ pageStates.swiperActiveIndex }} / {{ houseImageAndVideoList.length }}</div>
            </swiper-slide>            
        </swiper>
        <!---->
        <div class="wk-panel summary">
            <div class="panel-header">
                <div class="message">
                    <h1>{{ apiData.subEstateName }}</h1>
                    <div class="money">{{ apiData.rentPriceStr }}  / </div>
                </div>
                <!--<ul>
                    <li><span></span><span>20</span></li>
                    <li><span></span><span>140</span></li>
                    <li><span></span><span>88</span></li>
                </ul>-->
            </div>
        </div>
        <!---->
        <div class="wk-panel house-info">
            <div  class=" panel-body lr-padding tb-padding">
                <ul class="column-3">
                    <li v-if="apiData.isWKhouse==1"><dl><dt>{{ apiData.payTypeStr }}</dt><dd></dd></dl></li>
                    <li><dl><dt>{{ apiData.houseTypeStr }}</dt><dd></dd></dl></li>
                    <li><dl><dt>{{ apiData.spaceArea }} m&sup2;</dt><dd> </dd></dl></li>
                </ul>
                <hr>
                <ul class="outline">
                    <li class="percent-70"><dl><dt></dt><dd>{{ apiData.houseChildTypeStr||"--"}}</dd></dl></li>
                    <li class="percent-30"><dl><dt></dt><dd>{{ apiData.renovationStr||"--" }}</dd></dl></li>
                    <li class="percent-70"><dl><dt></dt><dd>{{ apiData.floorStr||"--"}}</dd></dl></li>
                    <li class="percent-30"><dl><dt></dt><dd>{{ apiData.orientationStr||"--" }}</dd></dl></li>
                    <li class="percent-100"><dl><dt></dt><dd>{{ apiData.estateAddr||"--"}}</dd></dl></li>
                </ul>
                <hr>
                <ul class="subway-school">
                    <li><dl><dt></dt><dd>{{ apiData.subwayStr||"--" }}</dd></dl></li>
                    <li><dl><dt></dt><dd>{{ apiData.schoolStr||"--" }}</dd></dl></li>
                </ul>
            </div>
        </div>
        <!---->
        <div class="wk-panel support top-gap">
            <div class="panel-header"></div>
            <ul  class="panel-body  lr-padding tb-padding">
                <li :class=" { no : ! apiData.houseSupporting.hasBad }" class="iconfont icon-bed "></li>
                <li :class=" { no : ! apiData.houseSupporting.hasTV }" class="iconfont icon-tv "></li>
                <li :class=" { no : ! apiData.houseSupporting.hasInternet }" class="iconfont icon-internet "></li>
                <li :class=" { no : ! apiData.houseSupporting.hasAirConditioning }" class="iconfont icon-airconditioner right"></li>
                <li :class=" { no : ! apiData.houseSupporting.hasWashMachine }" class="iconfont icon-washer"></li>
                <li :class=" { no : ! apiData.houseSupporting.hasWardrobe }" class="iconfont icon-wardrobe "></li>
                <li :class=" { no : ! apiData.houseSupporting.hasWaterHeater }" class="iconfont icon-heater "></li>
                <li :class=" { no : ! apiData.houseSupporting.hasHeating }" class="iconfont icon-heating right"></li>
                <li :class=" { no : ! apiData.houseSupporting.hasBalcony }" class="iconfont icon-balcony"></li>
                <li :class=" { no : ! apiData.houseSupporting.hasGas }" class="iconfont icon-gas "></li>
                <li :class=" { no : ! apiData.houseSupporting.hasMicrowave }" class="iconfont icon-microwave "></li>
                <li :class=" { no : ! apiData.houseSupporting.hasFridge }" class="iconfont icon-refrigerator right"></li>       
            </ul>
        </div>
        <!---->
        <div class="wk-panel description top-gap">
            <!---->
            <div v-if="apiData.isWKhouse==1" class="self-house">
                <div class="panel-header"></div>
                <div  class="panel-body  lr-padding tb-padding">
                    <dl><dt></dt><dd>{{ apiData.sellPoint || "" }}</dd></dl>
                    <dl><dt></dt><dd>{{ apiData.ownerMotivation || "" }}</dd></dl>
                    <dl><dt></dt><dd>{{ apiData.aroundSupport || "" }}</dd></dl>
                </div>
            </div>
            <!---->
            <div v-else-if="apiData.isWKhouse==2&&apiData.sellPoint.length>30" class="outside-house ">
                <div class="panel-header"></div>
                <div class="outside-info panel-body lr-padding" :class="{moreInfo:moreInfo}" ref="sansInfo">{{apiData.sellPoint}}</div>
                <div  v-if="moreInfo" @click="outsideMoreInfo" class="is-look lr-padding">{{isLook}}</div>
            </div>
        </div>
        <!---->
        <div class="wk-panel top-gap estate-info">
            <div class="info panel-header"></div>
            <div class="estate-detail">
                <router-link :to="'/estate/detail/share/'+apiData.encryptSubEstateId" :data-bigdata="getUvParamsString({ eventName : 2057004})">
                    <div class="estate-img">
                        <img :src="apiData.estateImgUrl" alt="">
                    </div>
                    <div class="estate-text">
                        <ul>
                            <li class="estate-name">{{apiData.subEstateName}}</li>
                            <li class="jiantou">
                                <span>{{apiData.completedStr}}</span><span class="division">|</span><span>{{apiData.totalHouse}}</span>
                                <i class="iconfont icon-arrowR skip"></i>
                                <!--<router-link :to="'/estate/detail/share/'+apiData.encryptSubEstateId" :data-bigdata="getUvParamsString({ eventName : 2057004})"></router-link>-->
                            </li>
                            <li>{{apiData.estateAddr}}</li>
                        </ul>
                    </div>
                </router-link>
            </div>
        </div>
        <!---->
        <div class="wk-panel location ">
            <a :href="mapUrl">
                <div class="location-container">
                    <img :src="'https://api.map.baidu.com/staticimage/v2?ak=GByG2pAz1WlmY7wX1rlIM4nd&width=640&height=426&center=' + apiData.longitude + ',' + apiData.latitude + '&zoom=18'" class="img-responsive">
                    <div class="cover">
                        <i class="iconfont icon-arrowTS"></i><span>{{ apiData.estateAddr}}</span>
                    </div>
                    <div class="mark"></div>
                </div>
            </a>
        </div>
        <!---->
        <div class="alike-house top-gap wk-panel" v-if="apiData.similarHouses.length!=0">
            <div class="house-recommend panel-header"></div>
            <rent-sources :statusStyle="styleStatus" :dataItems="apiData.similarHouses" :agentId="agentId" :eventName="2057005"></rent-sources>
        </div>
        <!---->
    </div>
</template>

<script>
    import downloadApp from "@/components/common/downloadApp" ;
    import assistant from "@/components/common/assistant" ;
    import rentSources from "@/components/common/rentSources";
    import $ from "jquery";
    import { swiper , swiperSlide } from "vue-awesome-swiper" ;
    import apiDataFilter from "@/libraries/apiDataFilter" ;
    import config from "@/configs/api";
    let prefix = config.prefix[apiDataFilter.getEnv()];
    export default {
      name : "rentDetailShare" ,
      data () {
          return {
              pageStates : {
                  swiperActiveIndex : 1 //
              } ,
              pageConfs : {
                  swiperOption : {  //  swiper
                      name : "currentSwiper" ,
                      // Swiper
                      autoplay : 0 , //msslide
                      grabCursor : true ,  //trueSwiper
                      setWrapperSize : true ,
                      autoHeight : false ,  //truewrappercontainerslide
                      //
                      onInit : swiper => {
                          this.pageStates.swiperActiveIndex = swiper.activeIndex + 1 ;
                      } ,
                      onSlideChangeEnd : swiper => {
                        this.pageStates.swiperActiveIndex = swiper.activeIndex + 1 ;
                      }
                  }
              } ,
              apiData : {
                houseVideos:{},
                houseSupporting:{},
                similarHouses:{},
                houseAgent:{}
              },
              moreInfo:true,//5
              textHeight:'',//
              isLook:'',//
              styleStatus:false,//
              agentId:'',
          }
      } ,
      methods : {
          //
        //   getUvParamsString : function({ eventName , otherParams }) {
        //     //   let eventParam = { house_id : this.apiData.houseId } ;
        //     if(otherParams){
        //         let eventParam = { house_id : this.apiData.houseId } ;
        //         eventParam = Object.assign( eventParam , otherParams ) ;
        //         return encodeURIComponent(JSON.stringify({
        //           eventName : eventName ,
        //           eventParam : eventParam ,
        //           type : 2
        //         }));
        //     }else{
        //         return encodeURIComponent(JSON.stringify({
        //           eventName : eventName ,
        //           type : 2
        //       })) ;
        //     }   
        //   },
          //
          getUvParamsString : function({ eventName , otherParams }) {
              let eventParam = { house_id : this.apiData.houseId } ;
              if(otherParams !== undefined && otherParams !== null ) {
                  eventParam = Object.assign( eventParam , otherParams ) ;
              }
              return encodeURIComponent(JSON.stringify({
                  eventName : eventName ,
                  eventParam : eventParam ,
                  type : 2
              })) ;
          },
         //
         outsideMoreInfo(){
            if($('.is-look').text()==''){
                this.$refs.sansInfo.style.height=this.textHeight+'px';
                this.isLook=''
            };
            if($('.is-look').text()==''){
                this.$refs.sansInfo.style.height=25*5+'px';
                this.isLook=''
            }
         },
        //  
         playVideo(video){
            this.$router.push({
                path:"/videoPlay?video=" + video
            });
         },
         watchRoute(){
                 let houseId = this.$route.params.houseId ;
          let agentId = this.$route.params.agentId ;
          this.cityId = this.$route.query.cityId;
          this.agentId=agentId;
		  let self = this;
          apiDataFilter.request({
              apiPath : "rent.detail" ,
              data : { "houseId" : houseId , "agentId" : agentId } ,
              successCallback : res => {
                  console.log(res)
                  this.apiData=Object.assign({}, res.body.data) ;
                  console.log(this.$data.apiData)
                  document.title = "" ;                  
                  this.$nextTick(function(){
                     if(this.apiData.isWKhouse==2){
                        let houseInfo=this.$refs.sansInfo.clientHeight;
                        this.textHeight=this.$refs.sansInfo.clientHeight;
                        //   ;
                        if(houseInfo/25>5){
                            this.$refs.sansInfo.style.height=25*5+'px';
                        }
                     }
                  });
                  //
                  let wechatShare = res.body.data.weChatShare ;
                  console.log(wechatShare);
                  this.$wechatShare({
                      "title" : wechatShare.title ,
                      "timelineTitle" : wechatShare.timelineTitle ,
                      "content" : wechatShare.content ,
                      "imgUrl" : wechatShare.picUrl ,
                      "success" : function() { console.log("") ;  } ,
                      "fail" : function() { console.log("") ;  } ,
                      "cancel" : function() { console.log("") ; } ,
                      "complete" : function() { console.log("") ; }
                  }) ;

                  //
                this.$bigData({
                  pageName : 2057 ,
                  pageParam : {
                      agent_id : agentId ,
                      house_id : this.apiData.houseId
                  } ,
                  type : 1
                }) ;
              }
          }) ;
         }
      } ,
      created() {
         this.watchRoute()
      } ,
      watch:{
          "$route":"watchRoute"
      },
      computed:{
            houseImageAndVideoList:function(){
                let result = [];
                if(this.apiData.houseVideos.videoUrl){
                    result.push({
                        isVideo: true,
                        video: encodeURIComponent(JSON.stringify({
                            videoUrl:this.apiData.houseVideos.videoUrl,
                            videoImage: this.apiData.houseVideos.imgUrl
                        })),
                        url:this.apiData.houseVideos.videoUrl,
                        videoUrl:this.apiData.houseVideos.videoUrl,
                        videoImage: this.apiData.houseVideos.imageUrl
                    });
                }
                if(this.apiData.houseImages){
                    this.apiData.houseImages.forEach(function(img){
                        result.push({
                            url:img                         
                        });
                    });
                }

                return result;
            },
            mapImgUrl:function(){
                return 'https://api.map.baidu.com/staticimage/v2?ak=GByG2pAz1WlmY7wX1rlIM4nd&width=640&height=426&center='+this.apiData.estate.longitude+','+this.apiData.estate.latitude+'&zoom=18';
            },
            mapUrl:function(){
                return prefix + '/esf/map.html?longitude=' + this.apiData.longitude + '&latitude=' + this.apiData.latitude + '&houseName='+this.apiData.subEstateName + '&houseAddress=' + this.apiData.estateAddr;
            }
        },
      components : {
          downloadApp ,
          assistant ,
          rentSources,
          swiper ,
          swiperSlide
      }
    }
</script>

<style lang="less" scoped>
    @import "../../../../src/less/rent/detail.less" ;
</style>



